@media (max-width: 768px) {
  .KanbanMotion {
    width: 100% !important;
  }
}

.KanbanMotion {
  height: inherit;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding-top: 20px;
  margin-left: 20px;
  align-items: flex-start;
  z-index: 10;
}

.HomeMotion {
  display: flex;
  padding-top: 9rem;
  flex-direction: column;
  z-index: 5;
}

.HomeMotion span {
  font-size: 4rem;
  font-weight: 700;
  color: white;
  align-self: center;
  margin-bottom: 3.5rem;
}

.HomeMotion a {
  display: flex !important;
  position: unset !important;
  border: none !important;
  align-self: center !important;
  padding: 0 !important;
}

.HomeMotion a > button {
  border-radius: 12px;
  background-color: #124d86;
  color: white;
  width: 150px;
  height: 60px;
  font-size: 20px;
  font-weight: 700;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  border: 2px solid #072e54;
}

.HomeMotion button:hover {
  background-color: white;
  color: #124d86;
  border: 5px solid #072e54;
  cursor: pointer;
}
